.pomodoro * {
  box-sizing: border-box;
}

.pomodoro {
  padding: 0;
  margin: 0;
  font-family: "Major Mono Display", monospace;
  line-height: 1.5;
}

.pomodoro .app {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #87d7cc;
  color: #2c888d;
}

.pomodoro h2 {
  color: #49b2b8;
  font-size: 30px;
  text-align: center;
  margin: 0;
}

.pomodoro .timer {
  color: #1f6e72;
  font-size: 150px;
  margin-bottom: 20px;
}

.pomodoro .buttons button {
  outline: none;
  border: none;
  font-size: 24px;
  background: #f7fca0;
  color: #878b32;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  padding: 15px 30px;
  cursor: pointer;
  transition: 0.3s ease all;
}

.pomodoro .buttons button:hover {
  border-radius: 10px;
  background: #f5f8ca;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.pomodoro .buttons button:not(:last-child) {
  margin-right: 10px;
}

@media (max-width: 780px) {
  .pomodoro h2 {
    font-size: 22px;
  }

  .pomodoro .timer {
    font-size: 80px;
  }

  .pomodoro .buttons button {
    font-size: 18px;
  }
}

